<template>
  <button
    class="px-4 py-2 rounded bg-red-500 text-white hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-400"
    @click="onReset"
  >
    Reset Demo Data
  </button>
</template>

<script>
import { resetDemo, seedDemoIfNeeded } from "@/demo/demoData";

export default {
  name: "ResetDemoButton",
  methods: {
    onReset() {
      const ok = typeof window !== "undefined" ? window.confirm("Reset demo data to the original seed?") : true;
      if (!ok) return;
      resetDemo();
      seedDemoIfNeeded();
      if (typeof window !== "undefined") window.location.reload();
    }
  }
};
</script>
